//#region Global Imports
import * as React from 'react';
import { TextFieldProps, OutlinedTextFieldProps } from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import { makeStyles } from '@material-ui/styles';
//import { Components } from '@hims/core';
//#endregion Global Imports

const comboStyles = makeStyles({
	root: {
		marginTop: '16px',
		marginBottom: '8px'
	},
	adornedEnd: {
		paddingRight: 0,
	}
});

const comboSelectStyles = makeStyles({
	notchedOutline: {
		borderTopLeftRadius: 0,
		borderBottomLeftRadius: 0,
	},
	input: {
		textTransform: 'uppercase',
		padding: '12.5px 16px',
	},
	root: {
		height: '2.90em'
	}
});

type IComboFieldProps = TextFieldProps | OutlinedTextFieldProps;

export interface IComboContainerProps {
	fieldValue?: string;
	selectValue?: string;
	selectName?: string;
	fieldName?: string;
	disableSelect?: boolean;
	fieldOnChange?: (e: any) => void;
	selectOnChange?: (e: any) => void;
}


export interface IComboMainFieldProps {
	items: any;
	defaultValue: string;
	type: string;
}

export const EligibleMembersField: React.FC<IComboFieldProps & IComboContainerProps> = (props: IComboFieldProps & IComboContainerProps): JSX.Element => {
	const items = [{ name: 'Named', value: 'named' }, { name: 'Max', value: 'max' }];

	return (
		<ComboField
			{...props}
			items={items}
			type={'number'}
			defaultValue={'named'}
		/>
	);
};

