import React, { HTMLAttributes, FC, useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import InputAdornment from '@material-ui/core/InputAdornment';
import TextField, { BaseTextFieldProps } from '@material-ui/core/TextField';
import NoSsr from '@material-ui/core/NoSsr';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import AsyncSelect from 'react-select/async'
// import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import { ValueType } from 'react-select/src/types';
import { ControlProps } from 'react-select/src/components/Control';
import { MenuProps } from 'react-select/src/components/Menu';
import { ViewField } from '../ViewField';
import Grid from '@material-ui/core/Grid';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import clsx from 'clsx';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';


import { IContractModal } from './IContractModal';

interface OptionType {
  label: string;
  value: string;
}

const industryList = {
	bpo: 'Business Processing Outsourcing (BPO)',
	marketing: 'Marketing and Advertising',
	it: 'Information Technology and Services',
};

const useStyles = makeStyles((theme: Theme) => ({
	dialog: {},
	dialogAction: {
		marginBottom: theme.spacing(2),
	},
	dialogContent: {
		overflow: 'visible'
	}
}));

const cardStyles = makeStyles({
	card: {
		marginBottom: '10px',
		maxWidth: 475,
        border: '1px solid #0000001A',
		boxShadow: 'none',
		opacity: 0.2
	},
	cardSelected: {
		marginBottom: '10px',
		maxWidth: 475,
		border: '1px solid #3AB77D',
		boxShadow: 'none'
	},
	content: {
		textAlign: 'center'
	},
	title: {
		// marginTop: '10px',
		fontSize: '14px',
		fontWeight: 'bold'
	},
	subtitle: {
		fontSize: '14px',
		// marginBottom: '10px',
	},
	imageArea: {
		width: '100%',
		backgroundColor: '#E6F5F6',
		textAlign: 'center',
		padding: '16px'
	},
});

const searchStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
      height: 250,
      minWidth: 290,
    },
    input: {
      display: 'flex',
      padding: 0,
      height: 'auto',
    },
    valueContainer: {
      display: 'flex',
      flexWrap: 'wrap',
      flex: 1,
      alignItems: 'center',
      overflow: 'hidden',
    },
    noOptionsMessage: {
      padding: theme.spacing(1, 2),
    },
    singleValue: {
      fontSize: 16,
    },
    placeholder: {
      position: 'absolute',
      left: 2,
      bottom: 6,
      fontSize: 16,
    },
    paper: {
      position: 'absolute',
      zIndex: 2000,
      marginTop: theme.spacing(1),
      left: 0,
      right: 0,
    },
    divider: {
      height: theme.spacing(2),
    },
  }),
);

const searchStyles2 = {
	indicatorsContainer: (provided) => {
		const padding = '14px 16px';
		const position = 'absolute';
    	const top = '-12px';
    	const right = '-6px';

		return { ...provided, position, top, right, padding };
	}
}


type InputComponentProps = Pick<BaseTextFieldProps, 'inputRef'> & HTMLAttributes<HTMLDivElement>;

const inputComponent = ({ inputRef, ...props }: InputComponentProps) => {
  return <div ref={inputRef} {...props} />;
}

const Control = (props: ControlProps<OptionType>) => {
  const {
    children,
    innerProps,
    innerRef,
    // selectProps: { classes, TextFieldProps },
    selectProps: { TextFieldProps },
  } = props;

  return (
    <TextField
      fullWidth
      InputProps={{
        inputComponent,
        inputProps: {
          // className: classes.input,
          ref: innerRef,
          children,
          ...innerProps,
        },
        startAdornment:
			<InputAdornment position="start">
				<FontAwesomeIcon icon={faSearch} />
			</InputAdornment>
      }}
      {...TextFieldProps}
    />
  );
}

const Menu = (props: MenuProps<OptionType>) => {
  return (
    <Paper square className={props.selectProps.classes.paper} {...props.innerProps}>
      {props.children}
    </Paper>
  );
}

const components = {
  Control,
  Menu,
};

export const ContractModal: FC<IContractModal.IProps> = props => {
	const classes = useStyles();
	const cardClasses = cardStyles();
	const searchClasses = searchStyles();
	const reset = () => setSearch(null);

	const [search, setSearch] = useState<ValueType<OptionType>>(null);

	const handleChangeSearch = (value: ValueType<OptionType>) => {
	    setSearch(value);
	};

	const handleClose = () => {
		reset();
		props.onClose();
	};

	let contract = 'sme';

	if (props.clientData.principalData.numOfPrincipals > 100) {
		contract = 'benefit';
	}

	return (
		<Dialog
			id="contract-modal"
			open={props.isModalOpen}
			onClose={handleClose}
			aria-labelledby="contract-modal-title"
			maxWidth="sm"
			className={classes.dialog}
		>
			<DialogTitle id="contract-modal-title">Create a Proposal</DialogTitle>
			<DialogContent className={classes.dialogContent}>
				<Grid container spacing={2} justify="space-between" alignItems="flex-start">
					<Grid item sm={6}>
						<ViewField 
							label="Company"
							value={props.clientData.businessData.registeredCorporateName}
						/>
					</Grid>
					<Grid item sm={6}>
						<ViewField 
							label="Principals"
							value={props.clientData.principalData.numOfPrincipals}
						/>
					</Grid>
					<Grid item sm={6}>
						<ViewField 
							label="Industry"
							value={industryList[props.clientData.natureData.industry]}
						/>
					</Grid>
					<Grid item sm={6}>
						<ViewField 
							label="Dependents"
							value={props.clientData.principalData.numOfDependents}
						/>
					</Grid>
					<Grid item sm={12}>
						<Typography align="center" variant="h6">This template is ideal for this company</Typography>
					</Grid>
					<Grid container spacing={2} justify="center" alignItems="flex-start">
						<Grid item xs={4}>
						    <Card className={contract === 'sme' ? cardClasses.cardSelected : cardClasses.card}>
						      	<CardActionArea disabled={contract !== 'sme'}>
					        		<div className={cardClasses.imageArea}>
					        			<img src="shop@2x.png" />
					        		</div>
						        	<CardContent className={cardClasses.content}>
						          		<Typography gutterBottom className={cardClasses.title} color="primary" variant="body1">
						            		Standard SME
						          		</Typography>
						          		<Typography className={cardClasses.subtitle} variant="body1">
						            		&lt; 100 employees
						          		</Typography>
						        	</CardContent>
						      	</CardActionArea>
						    </Card>
						</Grid>
						<Grid item xs={4}>
						    <Card className={contract === 'benefit' ? cardClasses.cardSelected : cardClasses.card}>
						      	<CardActionArea disabled={contract !== 'benefit'}>
					        		<div className={cardClasses.imageArea}>
						        		<img src="condo@2x.png" />
					        		</div>
						        	<CardContent className={cardClasses.content}>
						          		<Typography gutterBottom className={cardClasses.title} color="primary" variant="body1">
						            		Standard Benefit
						          		</Typography>
						          		<Typography className={cardClasses.subtitle} variant="body1">
						            		&gt; 100 employees
						          		</Typography>
						        	</CardContent>
						      	</CardActionArea>
						    </Card>
						</Grid>
					</Grid>
					{
						contract === 'benefit' ? 
						<React.Fragment>
							<Grid item sm={12}>
								<Typography align="center" variant="h6">Or you may use one of your Approved Proposals</Typography>
      							<NoSsr>
							        <AsyncSelect
		            					menuPortalTarget={document.getElementById("contract-modal")}
							          	placeholder="Search proposal"
	          							inputId="search-proposal"
	          							classes={searchClasses}
	          							styles={searchStyles2}
	          							components={components}
							          	TextFieldProps={{
							          		variant: 'outlined',
		            					}}
		            					cacheOptions
		            					defaultOptions
		            					loadOptions={props.loadOptions}
		            					onChange={handleChangeSearch}
		            					value={search}
							        />
      							</NoSsr>
							</Grid>
						</React.Fragment> : null
					}
						
				</Grid>
			</DialogContent>
			<DialogActions className={clsx(classes.dialogAction)}>
				<Grid container spacing={2} justify="center" alignItems="flex-start">
					<Grid item xs={3}>
						<Button
							onClick={handleClose}
							variant="outlined"
							fullWidth
						>
							Cancel
						</Button>
					</Grid>
					<Grid item xs={3}>
						<Button
							onClick={() => props.createProposal(contract, search)}
							variant="contained"
							color="primary"
							fullWidth
							disabled={contract === ''}
						>
							Create
						</Button>
					</Grid>
				</Grid>
			</DialogActions>
		</Dialog>
	);
};

ContractModal.defaultProps = {
	isModalOpen: false,
};
