//#region Global Imports
import * as React from 'react';

import { BenefitsTable } from '../../BenefitsTable';
import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Typography from '@material-ui/core/Typography';

import {
	CurrencyField,
	WaitingPeriodField,
} from '../../ComboField';
import { ViewField } from '../../ViewField';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';

//import { Components } from '@hims/core';
//#endregion Global Imports

import { defaultProps, radioStyles } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const MaternityForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, viewOnly, setSubForm, setSubForm2, subFormNode } = props;
	const radioClasses = radioStyles();

	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}

	// console.log(rows);

	// const fakeRows: any = [
	// 	{
	// 		provision: 'Pre-natal checkup',
	// 		eligible_availees: ['principals'],
	// 		limit_type: ['max_session'],
	// 		limit: [3],
	// 		availment: 'outright',
	// 	},
	// 	{
	// 		provision: 'Normal delivery',
	// 		eligible_availees: ['principals'],
	// 		limit_type: ['amt_limit'],
	// 		limit: [1000],
	// 		availment: 'reimbursement',
	// 	},
	// 	{
	// 		provision: 'Casaerean section',
	// 		eligible_availees: ['mothers', 'spouses'],
	// 		limit_type: ['amt_limit'],
	// 		limit: [1000],
	// 		availment: 'reimbursement',
	// 	},
	// 	{
	// 		provision: 'Ultrasound',
	// 		eligible_availees: ['all_dependents'],
	// 		limit_type: ['max_session'],
	// 		limit: [2],
	// 		availment: 'reimbursement',
	// 	},
	// 	{
	// 		provision: 'Consultation',
	// 		eligible_availees: ['principals'],
	// 		limit_type: ['amt_limit'],
	// 		limit: [2000],
	// 		availment: 'reimbursement',
	// 	},
	// ];

	const columns: any = [
		{ name: 'name', title: 'Provision' },
		{ 
			name: 'eligible_availees',
			title: 'Eligible Availees',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.eligible_availees 
								 	? row.custom_metadata.eligible_availees : []),
		},
		{ 
			name: 'limit_type', 
			title: 'Limit Type',
			getCellValue: row => (row.limits
									? row.limits : [])
		},
		{
			name: 'limit', 
			title: 'Limit',
			getCellValue: row => (row.limits
									? row.limits : [])
		},
		{ 
			name: 'availment',
			title: 'Availment',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.availment 
								 	? row.custom_metadata.availment : []),
		},
	];

	const formattedColumns = {
		maternity_eligible_availees: ['eligible_availees'],
		limit_type: ['limit_type'],
		limit: ['limit'],
		availment: ['availment'],
	};

	const columnExtensions = [
		{ columnName: 'limit_type', width: 150 },
		{ columnName: 'limit', width: 150 },
	];


	const billingArrangement = {
		imputed_in_premiums: "Imputed in premiums",
		billback: "Bill back with admin fee (15%)",
		rider_fee: "1-time rider fee to be upon enrollment"
	}

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					{...props}
				/>
			</Grid>
			<Grid item sm={12}>
				<Grid item sm={12}>
				{
					!viewOnly ? 
						<React.Fragment>
							<InputLabel htmlFor="availmentPerYear">Availment Per Year</InputLabel>
							<FormControl margin="normal" style={{marginBottom: '20px'}}>
								<OutlinedInput
									onChange={setSubForm} 
									value={metadata['availment_per_year'] ? metadata['availment_per_year'] : 0}
									type="number"
									labelWidth={0}
									name="availment_per_year"
									id="availmentPerYear"
								/>
							</FormControl>
						</React.Fragment> :
						<ViewField 
							label="Availment Per Year"
							value={metadata['availment_per_year'] ? metadata['availment_per_year'] : 0}
						/>
				}
				</Grid>

				<Grid item sm={12}>
					{
						!viewOnly ? 
							<React.Fragment>
								<Typography variant="overline" display="block" gutterBottom>
									Activation
								</Typography>
								<FormControl component="fieldset">
									<RadioGroup name="waiting_period">
										<FormControlLabel
											classes={radioClasses}
											value="waiting_period_effectivity"
											control={
														<Radio 
															checked={metadata['waiting_period_type'] === 'effectivity'} 
															name="waiting_period_type"
															onClick={(e) => {
																e.stopPropagation();
																setSubForm2("waiting_period_type", "effectivity");
															}}
														/>
													}
											label={
												<React.Fragment>
													<Typography variant="body1">
														Waiting period after effectivity of member
													</Typography>
													<FormControl>
														<WaitingPeriodField 
															disabled={metadata['waiting_period_type'] !== 'effectivity'}
															id="waiting_period_effectivity" 
															fieldName="waiting_period"
															selectName="waiting_period_unit"
															fieldValue={
																metadata['waiting_period_type'] === 'effectivity' ? 
																metadata['waiting_period'] : 0
															}
															selectValue={
																metadata['waiting_period_type'] === 'effectivity' ?
																metadata['waiting_period_unit'] : 'months'
															}
															fieldOnChange={setSubForm}
															selectOnChange={setSubForm}
														/>
													</FormControl>
												</React.Fragment>
											}
										/>
										<FormControlLabel
											classes={radioClasses}
											value="waiting_period_hire"
											control={
														<Radio 
															checked={metadata['waiting_period_type'] === 'date_of_hire'} 
															name="waiting_period_type"
															onClick={(e) => {
																e.stopPropagation();
																setSubForm2("waiting_period_type", "date_of_hire");
															}}
														/>
													}
											label={
												<React.Fragment>
													<Typography variant="body1">
														Waiting period after date of hire
													</Typography>
													<FormControl>
														<WaitingPeriodField 
															disabled={metadata['waiting_period_type'] !== 'date_of_hire'}
															id="waiting_period_hire" 
															fieldName="waiting_period"
															selectName="waiting_period_unit"
															fieldValue={
																metadata['waiting_period_type'] === 'date_of_hire' ? 
																metadata['waiting_period'] : 0
															}
															selectValue={
																metadata['waiting_period_type'] === 'date_of_hire' ?
																metadata['waiting_period_unit'] : 'months'
															}
															fieldOnChange={setSubForm}
															selectOnChange={setSubForm}
														/>
													</FormControl>
												</React.Fragment>
											}
										/>
									</RadioGroup>
								</FormControl>
							</React.Fragment> :
							<ViewField 
								label="Activation"
								value={	metadata['waiting_period_type'] && metadata['waiting_period_unit'] ? 
										metadata['waiting_period_type'] + " " + metadata['waiting_period_unit'] : ''
									}
							/>
					}
				</Grid>

				<Grid item sm={12}>
					{
						!viewOnly ? 
							<React.Fragment>
								<Typography variant="body1" style={{ marginTop: '20px' }} gutterBottom>
									Billing arrangement
								</Typography>
								<FormControl component="fieldset">
									<RadioGroup name="billing_arangement">
										<FormControlLabel
											value="premium"
											control={
												<Radio 
													checked={metadata['billing_arangement'] === 'premium'} 
													name="premium"
													onClick={(e) => {
														e.stopPropagation();
														setSubForm2("billing_arangement", "imputed_in_premiums");
													}}

												/>
											}
											label={billingArrangement['imputed_in_premiums']}
										/>
										<FormControlLabel
											value="billback"
											control={
												<Radio 
													checked={metadata['billing_arangement'] === 'billback'} 
													name="premium"
													onClick={(e) => {
														e.stopPropagation();
														setSubForm2("billing_arangement", "billback");
													}}

												/>
											}
											label={billingArrangement['billback']}
										/>
										<FormControlLabel
											classes={radioClasses}
											value="rider_fee"
											control={
												<Radio 
													checked={metadata['billing_arangement'] === 'rider_fee'} 
													name="rider_fee"
													onClick={(e) => {
														e.stopPropagation();
														setSubForm2("billing_arangement", "rider_fee");
													}}

												/>
											}
											label={
												<React.Fragment>
													<Typography variant="body1" gutterBottom>
														{billingArrangement['rider_fee']}
													</Typography>
													<Typography variant="body1">Fee</Typography>
													<CurrencyField 
														id="fee_field" 
														disabled={metadata['billing_arangement'] !== 'rider_fee'}
														fieldName="rider_fee"
														selectName="rider_fee_unit"
														fieldValue={
															metadata['billing_arangement'] === 'rider_fee' ? 
															metadata['rider_fee'] : 0
														}
														selectValue={
															metadata['billing_arangement'] === 'rider_fee' ?
															metadata['rider_fee_unit'] : 'PHP'
														}
														fieldOnChange={setSubForm}
														selectOnChange={setSubForm}
													/>
												</React.Fragment>
											}
										/>
									</RadioGroup>
								</FormControl>
							</React.Fragment> :
							<ViewField 
								label="Billing arrangement"
								value={billingArrangement[metadata['billing_arangement']]}
							/>
					}
				</Grid>
			</Grid>
		</React.Fragment>
	);
};

MaternityForm.defaultProps = defaultProps;