//#region Global Imports
import * as React from 'react';
import Grid from '@material-ui/core/Grid';
import InputLabel from '@material-ui/core/InputLabel';
import { CurrencyField } from '../ComboField';
import { ViewField } from '../ViewField';
import Hidden from '@material-ui/core/Hidden';
import Typography from '@material-ui/core/Typography';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileUpload } from '@fortawesome/free-solid-svg-icons';
import { GlobalFunction } from '../../GlobalFunction';
//import { Components } from '@hims/core';
//#endregion Global Imports

import { useDropzone } from 'react-dropzone';

import filesize from 'filesize';

interface BenefitDetailsFormProps {
	contractNode: any;
	setChangeBenefitForm?: (e) => void;
	viewOnly?: boolean;
	isSME?: boolean;
	classes: any;
}


const defaultProps: BenefitDetailsFormProps = {
	contractNode: {},
	setChangeBenefitForm: (e) => console.log("setChangeBenefitForm", e),
	viewOnly: false,
	classes: {},
	isSME: false
};

export const BenefitDetailsForm: React.FC<BenefitDetailsFormProps> = (props: BenefitDetailsFormProps): JSX.Element => {
	const {viewOnly, isSME, contractNode, setChangeBenefitForm, classes} = props;

	const onDrop = React.useCallback((acceptedFiles: any[]) => {
		console.log(acceptedFiles);
		// Do something with the files
	}, []);

	const { acceptedFiles, getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

	const files = acceptedFiles.map((file: any) => (
		<li key={file.path}>
			{file.path} - {filesize(file.size)}
		</li>
	));

	let metadata = {};

	if (contractNode && contractNode['custom_metadata'] !== undefined) {
		metadata = contractNode['custom_metadata'];
	}

	const displayValue = (inputValue: number, inputUnit: string) => {
		let unit = 'PHP';
		let value = 0;

		if (inputUnit) {
			unit = inputUnit;
		} 

		if (inputValue) {
			value = inputValue;
		} else {
			if (isSME) {
				return 'PHP 1,000,000';
			} else {
				return '-';
			}
		}

		return unit + " " + GlobalFunction.formatNumber(value);
	}

	return (
		<React.Fragment>
			<Grid item sm={3} style={{ marginTop: '40px'}}>
				{ !viewOnly ?
					<CurrencyField
						id="requestedPremium"
						label="Requested Premium"
						helperText="Suggested: 1,000,000"
						fieldName="requested_premium"
						selectName="requested_premium_unit"
						fieldValue={metadata['requested_premium']}
						selectValue={metadata['requested_premium_unit']}
						fieldOnChange={setChangeBenefitForm}
						selectOnChange={setChangeBenefitForm}
					/> :
					<ViewField 
						label="Requested Premium"
						value={displayValue(metadata['requested_premium'], metadata['requested_premium_unit'])}
					/>
				}
			</Grid>
			<Hidden xsDown>
				<Grid item sm={9}></Grid>
			</Hidden>

			{ !isSME ?
				<Grid item sm={4} style={{ marginTop: '60px' }}>
					<Typography  className={classes.fieldGroupTitle} gutterBottom>
						REASON FOR THE REQUEST
					</Typography>
				</Grid> : null
			}

			{ !isSME ?
				<Hidden xsDown>
					<Grid item sm={8}></Grid>
				</Hidden> : null
			}


			{ !isSME ?
				<Grid item sm={6}>
					<Grid container spacing={2}>
						<Grid item sm={6}>
							{ !viewOnly ?
								<CurrencyField 
									id="totalCompanyBudget" 
									label="Total Company Budget" 
									fieldName="total_company_budget"
									selectName="total_company_budget_unit"
									fieldValue={metadata['total_company_budget']}
									selectValue={metadata['total_company_budget_unit']}
									fieldOnChange={setChangeBenefitForm}
									selectOnChange={setChangeBenefitForm}
								/> :
								<ViewField 
									label="Total Company Budget"
									value={displayValue(metadata['total_company_budget'], metadata['total_company_budget_unit'])}
								/>
							}
						</Grid>
						<Grid item sm={6}>
							{ !viewOnly ?
								<CurrencyField 
									id="competitorsRates" 
									label="Competitors Rates" 
									fieldName="competitors_rates"
									selectName="competitors_rates_unit"
									fieldValue={metadata['competitors_rates']}
									selectValue={metadata['competitors_rates_unit']}
									fieldOnChange={setChangeBenefitForm}
									selectOnChange={setChangeBenefitForm}
								/> :
								<ViewField 
									label="Competitors Rates"
									value={displayValue(metadata['competitors_rates'], metadata['competitors_rates_unit'])}
								/>
							}
						</Grid>
						<Grid item sm={6}>
							{ !viewOnly ?
								<CurrencyField 
									id="budgetPerHead" 
									label="Budget per Head" 
									fieldName="budget_per_head"
									selectName="budget_per_head_unit"
									fieldValue={metadata['budget_per_head']}
									selectValue={metadata['budget_per_head_unit']}
									fieldOnChange={setChangeBenefitForm}
									selectOnChange={setChangeBenefitForm}
								/> :
								<ViewField 
									label="Budget per Head"
									value={displayValue(metadata['budget_per_head'], metadata['budget_per_head_unit'])}
								/>
							}
						</Grid>
						<Grid item sm={6}>
							{ !viewOnly ?
								<CurrencyField 
									id="ownComputation" 
									label="Own Computation" 
									fieldName="own_computation"
									selectName="own_computation_unit"
									fieldValue={metadata['own_computation']}
									selectValue={metadata['own_computation_unit']}
									fieldOnChange={setChangeBenefitForm}
									selectOnChange={setChangeBenefitForm}
								/> :
								<ViewField 
									label="Own Computation"
									value={displayValue(metadata['own_computation'], metadata['own_computation_unit'])}
								/>
							}
						</Grid>
					</Grid>
				</Grid> : null
			}

			{ !isSME ?
				<Grid item sm={6}>
					{ !viewOnly ?
						<React.Fragment>
							<InputLabel htmlFor="computationsFile">Upload Computation</InputLabel>
							<div {...getRootProps({ className: classes.dropzone })}>
								<input {...getInputProps()} />
								{isDragActive ? (
									<p>Click or drag/drop files</p>
								) : (
									<React.Fragment>
										<FontAwesomeIcon icon={faFileUpload} />
										<Typography>Click or drag/drop files</Typography>
									</React.Fragment>
								)}
							</div>
							<aside>
								{files.length > 0 ? (
									<div>
										<Typography>Uploaded File</Typography>
										<ul>{files}</ul>
									</div>
								) : null}
							</aside>
						</React.Fragment> :
						<React.Fragment>
							{files.length > 0 ? (
									<div>
										<Typography>Uploaded File</Typography>
										<ul>{files}</ul>
									</div>
								) : null}
						</React.Fragment>
					}
				</Grid> : null
			}
		</React.Fragment>
	);
};

BenefitDetailsForm.defaultProps = defaultProps;
