import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import { InputLabel, TextField } from '@material-ui/core';

// Import Material-UI props interface
import {
  BaseTextFieldProps,
  TextFieldProps,
  OutlinedTextFieldProps,
} from '@material-ui/core/TextField';

/**
 * label - name label displayed above the text field
 * width - width of the text field
 */
interface BasicTextFieldProps {
  id?: string;
  label?: string;
  width?: string;
  value?: BaseTextFieldProps['value'];
  onChange?: OutlinedTextFieldProps['onChange'];
  TextFieldProps?: TextFieldProps;
  type?: string;
  error?: boolean;
  disabled?: boolean;
  InputProps?: any;
}

const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
  },
  inputLabel: {
    color: '#272e4c',
  },
  textField: (props: BasicTextFieldProps) => ({
    marginTop: '10px',
    marginBottom: '20px',
    width: props.width,
  }),
  outlinedInputInput: {
    padding: '12.5px 14px',
  },
});

const BasicTextField = (props: BasicTextFieldProps) => {
  const classes = useStyles(props);

  let textFieldProps = props.TextFieldProps;
  textFieldProps = { ...textFieldProps, variant: 'outlined' };
  let inputProps = props.InputProps;
  inputProps = { ...inputProps, classes: {input: classes.outlinedInputInput} };
  return (
    <div className={classes.root}>
      <InputLabel htmlFor={props.id} className={classes.inputLabel}>
        {props.label}
      </InputLabel>
      <TextField
        id={props.id}
        className={classes.textField}
        inputProps={inputProps}
        value={props.value}
        onChange={props.onChange}
        {...textFieldProps}
        error={props.error ? props.error : false}
        disabled={props.disabled ? props.disabled : false}
      />
    </div>
  );
};

export default BasicTextField;
