export class Processmaker {
    static getWithToken(url:string, token:string){
        console.log('token: ',token);
    	let URL = API_URL + url;
    	console.log('get url: ', URL, token);
        return fetch(URL, {
		   	method: 'GET',
		   	headers: {
		   		'Content-Type':'application/json',
		   		'Authorization': 'Bearer ' + localStorage.getItem('pm_token')
		   	}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static postWithToken(url:string, token:string, body:any){
        console.log('token: ',token);
    	let URL = API_URL + url;
    	console.log('post url: ', URL, body);
        return fetch(URL, {
		   	method: 'POST',
		   	headers: {
		   		'Content-Type':'application/json',
		   		'Authorization': 'Bearer ' + localStorage.getItem('pm_token')
		   	},
		   	body: JSON.stringify(body)
		}).then(response => {
			console.log('response',response);
			return response.json()}
		).catch(error => {
			console.log('client error: ', error);
		});
	}
	
	static put(url:string, token:string, body:any){
        console.log('token: ',token);
    	let URL = API_URL + url;
    	console.log('post url: ', URL, body);
        return fetch(URL, {
		   	method: 'PUT',
		   	headers: {
		   		'Content-Type':'application/json',
		   		'Authorization': 'Bearer ' + localStorage.getItem('pm_token')
		   	},
		   	body: JSON.stringify(body)
		}).then(response => {
			console.log('response',response);
			return response.json()}
		).catch(error => {
			console.log('client error: ', error);
		});
    }

    static post(url:string, body:any){
    	let URL = API_URL + url;
    	console.log('post url: ', URL, body);
        return fetch(URL, {
		   	method: 'POST',
		   	headers: {
		   		'Content-Type':'application/json',
		   	},
		   	body: JSON.stringify(body)
		}).then(response => {
			console.log('response',response);
			return response.json()}
		).catch(error => {
			console.log('client error: ', error);
		});
	}
	
	static postLogin(url:string, body:any){
    	let URL = ROOT_URL + url;
    	console.log('post url: ', URL, body);
        return fetch(URL, {
		   	method: 'POST',
		   	headers: {
		   		'Content-Type':'application/json',
		   	},
		   	body: JSON.stringify(body)
		}).then(response => {
			console.log('response',response);
			return response.json()}
		).catch(error => {
			console.log('client error: ', error);
		});
	}

	static postClaim(url:string){
        return fetch(API_URL+url, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': 'Bearer ' + localStorage.getItem('pm_token')
			}
		}).then(response => response.json()
		).catch(error => {
			console.log('client error: ', error);
		});
    }
}