//#region Global Imports
import * as React from 'react';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import {
	InpatientForm,
	RoomAndBoardForm,
	DefaultForm,
	APEForm,
	PPEForm,
	OutpatientForm,
	EmergencyForm,
	DentalForm,
	OutpatientMedicineForm,
	ExclusionsForm,
	PartnerNetworkForm,
	SpecialDiagnosticForm,
	MaternityForm,
	SurehealthForm,
	PreventiveCareForm,
	ADDForm,
	CorporateClinicForm,
	ExecutiveCheckupForm,
	EligibilityForm,
} from './CategorySubForm';
//import { Components } from '@hims/core';
//#endregion Global Imports

interface CategoryFormProps {
	selectedPlanType: string;
	selectedBenefitCategory: string;
	subFormNode: any;
	rows: any[];
	setRowMetadata?: any;
	setRowMetadata2?: any;
	setRowLimits?: any;
	setRowSelection?: any;
	setSubForm?: any;
	setSubForm2?: any;
	createNode?: any;
	viewOnly?: boolean;
}

const defaultProps: CategoryFormProps = {
	selectedPlanType: '',
	selectedBenefitCategory: '',
	subFormNode: {},
	rows: [],
	setRowMetadata: (a, b) => console.log("setRowMetadata", a, b),
	setRowMetadata2: (a, b, c) => console.log("setRowMetadata2", a, b, c),
	setRowLimits: (a, b) => console.log("setRowMetadata", a, b),
	setRowSelection: (e) => console.log("setRowSelection", e),
	setSubForm: (e) => console.log("setSubForm", e),
	setSubForm2: (e) => console.log("setSubForm", e),
	createNode: (e) => console.log("createNode", e),
	viewOnly: false
};

export const CategoryForm: React.FC<CategoryFormProps> = (
	props: CategoryFormProps,
): JSX.Element | null => {
	const { rows, selectedPlanType, selectedBenefitCategory } = props;
	let formHeader: any = null;

	if (selectedPlanType === 'BASE') {
		formHeader = (
			<Grid item sm={12}>
				<Typography variant="h6">Base Plan</Typography>
				<Typography variant="body1" gutterBottom>
					You can use everything in this base plan or <Link>create your own</Link>
				</Typography>
			</Grid>
		);
	}

	if (selectedBenefitCategory === 'RBC') {
		return <RoomAndBoardForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'PE') {
		return <APEForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'PPE') {
		return <PPEForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'IP') {
		return <InpatientForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'OP') {
		return <OutpatientForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'DNT') {
		return <DentalForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'EM') {
		return <EmergencyForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'OM') {
		return <OutpatientMedicineForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'EX') {
		return <ExclusionsForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'NT') {
		return <PartnerNetworkForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'SD') {
		return <SpecialDiagnosticForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'MT') {
		return <MaternityForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'SH') {
		return <SurehealthForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'PV') {
		return <PreventiveCareForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'ADD') {
		return <ADDForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'CC') {
		return <CorporateClinicForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'EC') {
		return <ExecutiveCheckupForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'EL') {
		return <EligibilityForm formHeader={formHeader} {...props} />;
	}

	if (rows.length > 0) {
		return <DefaultForm formHeader={formHeader} {...props} />;
	} else {
		return null;
	}
};

CategoryForm.defaultProps = defaultProps;
