//#region Global Imports
import * as React from 'react';
import { TextFieldProps, OutlinedTextFieldProps } from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import InputLabel from '@material-ui/core/InputLabel';
import { makeStyles } from '@material-ui/styles';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Typography from '@material-ui/core/Typography';
//import { Components } from '@hims/core';
//#endregion Global Imports

const rangeStyles = makeStyles({
	root: {
		marginTop: '16px',
		marginBottom: '8px',
		width: '200px',
	},
	adornedStart: {
		paddingLeft: 0,
	}
});

const rangeAdornmentStyles = makeStyles({
	notchedOutline: {
		borderTopRightRadius: 0,
		borderBottomRightRadius: 0,
		width: '65px',
	},
	input: {
		width: '35px',
		padding: '12px 14px',
		textTransform: 'uppercase',
	},
	root: {
		height: '2.90em'
	}
});

type IRangeFieldContainerProps = TextFieldProps | OutlinedTextFieldProps;

export interface IRangeFieldProps {
	minValue?: string;
	maxValue?: string;
	minName?: string;
	maxName?: string;
	minOnChange?: (e: any) => void;
	maxOnChange?: (e: any) => void;
}


export const RangeField: React.FC<IRangeFieldProps & IRangeFieldContainerProps> = (props: IRangeFieldProps & IRangeFieldContainerProps): JSX.Element => {
	const rangeClasses = rangeStyles();
	const rangeAdornmentClasses = rangeAdornmentStyles();

	const { id, label, minValue, maxValue, minName, maxName, minOnChange, maxOnChange  } = props;

	const fields = [
		{
			id: 'Min',
			name: minName,
			value: minValue,
			onChange: minOnChange
		},
		{
			id: 'Max',
			name: maxName,
			value: maxValue,
			onChange: maxOnChange
		}
	];

	return (
		<React.Fragment>
			<InputLabel htmlFor={id + '_label'}>{label}</InputLabel>
			{fields.map((field: any, i: number) => {
				return (
					<React.Fragment>
						<OutlinedInput
							key={i}
							id={id}
							value={field.value}
							onChange={field.onChange}
							name={field.name}
							classes={rangeClasses}
							type="number"
							labelWidth={0}
							startAdornment = {(
								<InputAdornment position="start">
									<OutlinedInput
										classes={rangeAdornmentClasses}
										labelWidth={0}
										id={id + '_adornment'}
										value={field.id}
										disabled
									/>
								</InputAdornment>
							)}
						/>
						{i === 0 ? (
							<Typography
								style={{
									marginTop: '1.5em',
									marginLeft: '5px',
									marginRight: '5px',
									display: 'inline-block',
								}}
								variant="body1"
							>
								
							</Typography>
						) : null}
					</React.Fragment>
				);
			})}
		</React.Fragment>
	);
};
