/**
 * The global state selectors
 */

import { createSelector } from 'reselect';

const combineNodes = (parentNodes, childNodes) => {
  const nodes = parentNodes.reduce ((result, parent) => {
    parent.children.map ((child) => {
      let foundChild = childNodes.find ((childNode) => child === childNode.id);

      if (foundChild) {
        let child = Object.assign({}, foundChild);

        child['parent_name'] = parent.name;
        child['parent_code'] = parent.code;
        child['disabled'] = false;
        result.push(child);
      }
    });

    return result;
  }, new Array());

  return nodes;
};

const combineNodes2 = (parentNodes, childNodes) => {
  const nodes = parentNodes.reduce ((result, parent) => {

    let newParent = Object.assign({}, parent);
    let newChildNodes: any[] = []; 

    newParent.children.map ((child) => {
      let foundChild = childNodes.find ((childNode) => child === childNode.id);

      if (foundChild) {
        let child = Object.assign({}, foundChild);

        child['disabled'] = false;
        newChildNodes.push(child);
      }
    });

    newParent['child_nodes'] = newChildNodes;
    newParent['disabled'] = false;

    result.push(newParent);

    return result;
  }, new Array());

  return nodes;
};


const getFilteredNodes = (nodes, level) => {
  return nodes.filter((item: any) => {
    return (
      item.level === level
    );
  });
};

const getSidebarList = (nodes, planType, planPackage) => {
  return nodes.filter((item: any) => {
    return (
      item.tree_path[2] === planType &&
      item.tree_path[3] === planPackage
    );
  });
};

const getRows = (benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  if (['EX'].includes(selectedBenefitCategory)) {
    const categoryNodes = benefitCategoryNodes.filter ((item: any) => {
      return item.tree_path[2] === selectedPlanType &&
      item.tree_path[3] === selectedBenefitCategory;
    });
    const typeNodes = benefitTypeNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedBenefitCategory
      );
    });

    return combineNodes (categoryNodes, typeNodes);

  } else if (['NT'].includes(selectedBenefitCategory)) {
    const categoryNodes = benefitCategoryNodes.filter ((item: any) => {
      return item.tree_path[2] === selectedPlanType &&
      item.tree_path[3] === selectedBenefitCategory;
    });
    const typeNodes = benefitTypeNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedBenefitCategory
      );
    });

    return combineNodes2 (categoryNodes, typeNodes);

  } else if (['EL'].includes(selectedBenefitCategory)) {
    const benefitNodes = mainBenefitNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedPlanPackage &&
        item.tree_path[4] === selectedBenefitCategory &&
        item.tree_path[5] === 'DP'
      );
    });

    const nodes = subBenefitNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedPlanPackage &&
        item.tree_path[4] === selectedBenefitCategory &&
        item.tree_path[5] === 'DP'
      );
    });

    let combinedNodes = combineNodes2 (benefitNodes, nodes);

    if (selectedPlanType !== 'BASE') {
        combinedNodes = combinedNodes.map ((item) => {
          item.child_nodes.map((child) => {
            const baseNode = subBenefitNodes.find ((baseItem) => {
              return (
                baseItem.name === child.name &&
                baseItem.code === child.code &&
                baseItem.tree_path[2] === 'BASE' &&
                baseItem.tree_path[3] === child.tree_path[3] &&
                baseItem.tree_path[4] === child.tree_path[4] &&
                baseItem.tree_path[5] === child.tree_path[5] &&
                baseItem.tree_path[6] === child.tree_path[6] &&
                baseItem.tree_path[7] === child.tree_path[7]
              )
            });

            if (baseNode && baseNode.included === true) {
              child['disabled'] = true;
            } else {
              child['disabled'] = false;
            }

            return child;
          })
          return item;
      });
    }

    return combinedNodes;

  } else if (['RBC'].includes(selectedBenefitCategory))  {
    let nodes = [...benefitCategoryNodes];

    nodes = nodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedPlanPackage 
      );
    });

    nodes = nodes.map ((item) => {
      item['disabled'] = false;
      return item;
    });

    return nodes;
  } else if (['EM','DNT'].includes(selectedBenefitCategory)) {

    let nodes = [...benefitTypeNodes];

    nodes = nodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType  &&
        item.tree_path[3] === selectedPlanPackage &&
        item.tree_path[4] === selectedBenefitCategory
      );
    });

    if (selectedPlanType !== 'BASE') {
      nodes = nodes.map ((item) => {
        const baseNode = benefitTypeNodes.find ((baseItem) => {
          return (
            baseItem.name === item.name &&
            baseItem.code === item.code &&
            baseItem.tree_path[2] === 'BASE' &&
            baseItem.tree_path[3] === item.tree_path[3] &&
            baseItem.tree_path[4] === item.tree_path[4] &&
            baseItem.tree_path[5] === item.tree_path[5] 
          )
        });

        if (baseNode && baseNode.included === true) {
          item['disabled'] = true;
        } else {
          item['disabled'] = false;
        }

        return item;
      });
    } else {
      nodes = nodes.map ((item) => {
        item['disabled'] = false;
        return item;
      });
    }

    return nodes;
  } else if (['MT','PE', 'IP', 'OP', 'OM'].includes(selectedBenefitCategory)) {
    const typeNodes = benefitTypeNodes.filter ((item: any) => {
      return item.tree_path[2] === selectedPlanType &&
      item.tree_path[4] === selectedBenefitCategory;
    });
    const nodes = mainBenefitNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType &&
        item.tree_path[3] === selectedPlanPackage &&
        item.tree_path[4] === selectedBenefitCategory
      );
    });

    let combinedNodes = combineNodes (typeNodes, nodes);

    if (selectedPlanType !== 'BASE') {
      combinedNodes = combinedNodes.map ((item) => {
        const baseNode = mainBenefitNodes.find ((baseItem) => {
          return (
            baseItem.name === item.name &&
            baseItem.code === item.code &&
            baseItem.tree_path[2] === 'BASE' &&
            baseItem.tree_path[3] === item.tree_path[3] &&
            baseItem.tree_path[4] === item.tree_path[4] &&
            baseItem.tree_path[5] === item.tree_path[5] &&
            baseItem.tree_path[6] === item.tree_path[6]
          )
        });

        if (baseNode && baseNode.included === true) {
          item['disabled'] = true;
        } else {
          item['disabled'] = false;
        }

        return item;
      });
    }

    return combinedNodes;
  }

  const nodes = mainBenefitNodes.filter((item: any) => {
      return (
        item.tree_path[2] === selectedPlanType &&
        item.tree_path[3] === selectedPlanPackage &&
        item.tree_path[4] === selectedBenefitCategory
      );
    }
  );

  return nodes;
};

const getPlanTypeList = (planTypeNodes, mainBenefitNodes, benefitTypeNodes) => {
  return planTypeNodes.map((item: any) => {
    item['count'] = mainBenefitNodes.reduce ((result, benefit) => {
      if (benefit.tree_path[2] === item.code && benefit.included) {
        if (benefit.tree_path[2] !== 'BASE') {
          const baseNode = mainBenefitNodes.find ((baseItem) => {
            return (
              baseItem.name === benefit.name &&
              baseItem.code === benefit.code &&
              baseItem.tree_path[2] === 'BASE' &&
              baseItem.tree_path[3] === benefit.tree_path[3] &&
              baseItem.tree_path[4] === benefit.tree_path[4] &&
              baseItem.tree_path[5] === benefit.tree_path[5] &&
              baseItem.tree_path[6] === benefit.tree_path[6]
            )
          });

          if (baseNode && !baseNode.included) {
            result++;
          } 
        } else {
          result++;
        }
      }
      return result;
    }, 0);

    item['count'] = benefitTypeNodes.reduce ((result, benefit) => {
      if (  benefit.tree_path[2] === item.code && 
            ['CV'].includes(benefit.tree_path[3]) &&  
            ['EM'].includes(benefit.tree_path[4]) &&
            benefit.included 
        ) {
        if (  benefit.tree_path[2] !== 'BASE' ) {
          const baseNode = benefitTypeNodes.find ((baseItem) => {
            return (
              baseItem.name === benefit.name &&
              baseItem.code === benefit.code &&
              baseItem.tree_path[2] === 'BASE' &&
              baseItem.tree_path[3] === benefit.tree_path[3] &&
              baseItem.tree_path[4] === benefit.tree_path[4] &&
              baseItem.tree_path[5] === benefit.tree_path[5] 
            )
          });

          if (baseNode && !baseNode.included) {
            result++;
          } 
        } else {
          result++;
        }
      }
      return result;
    }, item['count']);


    return item;
  });
};

const getContractNode = (contractNodes) => {
  return contractNodes.find(item => 
    item.code === 'CT'
  );
};

const getCategoryNode = (planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  if (['RBC'].includes(selectedBenefitCategory)) {
    return planPackageNodes.find(item => 
      item.code === selectedPlanPackage &&
      item.tree_path[2] === selectedPlanType && 
      item.tree_path[3] === selectedPlanPackage 
    );
  } else if (selectedBenefitCategory === 'NT') {
      return planPackageNodes.find(item => 
        item.code === selectedBenefitCategory &&
          item.tree_path[2] === selectedPlanType && 
          item.tree_path[3] === selectedBenefitCategory 
        );
  } else {
    return benefitCategoryNodes.find(item => 
      item.code === selectedBenefitCategory &&
      item.tree_path[2] === selectedPlanType && 
      item.tree_path[3] === selectedPlanPackage && 
      item.tree_path[4] === selectedBenefitCategory
    );
  }
};

const getBenefitNodes = (state: any) => {
  return state.pages.createProposal.benefitNodes;
};

const getNewNodeCounter = (state: any) => {
  return state.pages.createProposal.newNodeCounter;
}

const getClientData = (state: any) => {
  return state.pages.createProposal.clientData;
};

const getContractType = (state: any) => {
  return state.pages.createProposal.contractType;
};

const getSelectedPlanType = (state: any) => {
  return state.pages.createProposal.selectedPlanType;
};

const getSelectedPlanPackage = (state: any) => {
  return state.pages.createProposal.selectedPlanPackage;
};

const getSelectedBenefitCategory = (state: any) => {
  return state.pages.createProposal.selectedBenefitCategory;
};


const selectBusinessData = createSelector(getClientData, (clientData) => {
  return clientData.businessData;
});

const selectNatureData = createSelector(getClientData, (clientData) => {
  return clientData.natureData;
});

const selectContactDetailsData = createSelector(getClientData, (clientData) => {
  return clientData.contactDetailsData;
});

const selectHMOData = createSelector(getClientData, (clientData) => {
  return clientData.HMOData;
});

const selectPrincipalData = createSelector(getClientData, (clientData) => {
  return clientData.principalData;
});

const selectContractNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 2);
});

const selectPlanTypeNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 3);
});

const selectPlanPackageNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 4);
});

const selectBenefitCategoryNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 5);
});

const selectBenefitTypeNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 6);
});

const selectMainBenefitNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 7);
});

const selectSubBenefitNodes = createSelector(getBenefitNodes, (benefitNodes) => {
  return getFilteredNodes (benefitNodes, 8);
});

const selectContractNode = createSelector(selectContractNodes, (contractNodes) => {
  return getContractNode(contractNodes);
});

const selectCategoryNode = createSelector(selectPlanPackageNodes, selectBenefitCategoryNodes, getSelectedPlanType, getSelectedPlanPackage, getSelectedBenefitCategory, (planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  return getCategoryNode(planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory);
});

const selectPlanTypeList = createSelector(selectPlanTypeNodes, selectMainBenefitNodes, selectBenefitTypeNodes, (planTypeNodes, mainBenefitNodes, benefitTypeNodes) => {
  return getPlanTypeList(planTypeNodes, mainBenefitNodes, benefitTypeNodes);
});

const selectRows = createSelector(selectBenefitCategoryNodes, selectBenefitTypeNodes, selectMainBenefitNodes, selectSubBenefitNodes, getSelectedPlanType, getSelectedPlanPackage, getSelectedBenefitCategory, (benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
  return getRows(benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory);
});

const selectRoomAndBoardList = createSelector(selectBenefitCategoryNodes, getSelectedPlanType, (benefitCategoryNodes, selectedPlanType) => {
  return getSidebarList (benefitCategoryNodes, selectedPlanType, 'RB');
});

const selectCoverageList = createSelector(selectBenefitCategoryNodes, getSelectedPlanType, (benefitCategoryNodes, selectedPlanType) => {
  return getSidebarList (benefitCategoryNodes, selectedPlanType, 'CV');
});

const selectRiderList = createSelector(selectBenefitCategoryNodes, getSelectedPlanType, (benefitCategoryNodes, selectedPlanType) => {
  return getSidebarList (benefitCategoryNodes, selectedPlanType, 'RI');
});

export {
  getBenefitNodes,
  getClientData,
  getSelectedPlanType,
  getSelectedPlanPackage,
  getSelectedBenefitCategory,
  selectCategoryNode,
  selectContractNode,
  selectPlanTypeList,
  selectRoomAndBoardList,
  selectCoverageList,
  selectRiderList,
  selectRows,
  selectBusinessData,
  selectNatureData,
  selectContactDetailsData,
  selectHMOData,
  selectPrincipalData,
  getContractType,
  getFilteredNodes,
  getSidebarList,
  getRows,
  getPlanTypeList,
  getContractNode,
  getCategoryNode,
  getNewNodeCounter
};
