import React, { FC, useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import { makeStyles, Theme } from '@material-ui/core/styles';
import clsx from 'clsx';

import { Components } from '@hims/core';

import { IAddPlanTypeModal } from './IAddPlanTypeModal';

const InputTextField = Components.UI.InputTextField.InputTextField;

const useStyles = makeStyles((theme: Theme) => ({
	dialog: {},
	dialogAction: {
		marginBottom: theme.spacing(2),
	},
}));

export type IDataField = IAddPlanTypeModal.IDataField;

export const AddPlanTypeModal: FC<IAddPlanTypeModal.IProps> = props => {
	const classes = useStyles();

	const [name, setName] = useState<string>('');
	const [nameError, setNameError] = useState<boolean>(false);
	const [code, setCode] = useState<string>('');
	const [codeError, setCodeError] = useState<boolean>(false);
	const reset = () => {
		setName('');
		setNameError(false);
		setCode('');
		setCodeError(false);
	};

	const handleClose = () => {
		reset();
		props.onClose();
	};

	const handleSubmit = () => {
		if (name.length === 0) {
			setNameError(true);
		}

		if (code.length === 0) {
			setCodeError(true);
		}

		if (name.length > 0 && code.length > 0) {
			const data: IDataField = {
				name: name,
				code: code,
			};

			reset();
			props.onSubmit(data);
			props.onClose();
		}
	};

	return (
		<Dialog
			open={props.isModalOpen}
			onClose={handleClose}
			aria-labelledby="form-dialog-title"
			maxWidth="md"
			className={classes.dialog}
		>
			<DialogTitle id="form-dialog-title">Add New Plan Type</DialogTitle>
			<DialogContent>
				<Grid container spacing={2} justify="space-between" alignItems="flex-start">
					<Grid item xs={12}>
						<InputTextField
							label="Plan Type Name"
							value={name}
							error={nameError}
							onChange={(value: string) => {
								setName(value);
								setNameError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item xs={12}>
						<InputTextField
							label="Plan Type Code"
							value={code}
							error={codeError}
							onChange={(value: string) => {
								setCode(value);
								setCodeError(value.length === 0 || value === 'BASE');
							}}
						/>
					</Grid>
				</Grid>
			</DialogContent>
			<DialogActions className={clsx(classes.dialogAction)}>
				<Grid container spacing={2} justify="center" alignItems="flex-start">
					<Grid item xs={4}>
						<Button onClick={handleClose} variant="outlined" color="default" fullWidth>
							Cancel
						</Button>
					</Grid>
					<Grid item xs={5}>
						<Button
							onClick={handleSubmit}
							variant="contained"
							color="primary"
							fullWidth
						>
							Add Plan Type
						</Button>
					</Grid>
				</Grid>
			</DialogActions>
		</Dialog>
	);
};

AddPlanTypeModal.defaultProps = {
	isModalOpen: false,
};
