//#region Global Imports
import * as React from 'react';

import { BenefitsTable } from '../../BenefitsTable';

import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import Typography from '@material-ui/core/Typography';

import {
	LimitField,
} from '../../ComboField';
import { ViewField } from '../../ViewField';

//import { Components } from '@hims/core';
//#endregion Global Imports


import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const OutpatientForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, viewOnly, setSubForm, subFormNode } = props;

	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}

	// console.log(rows);

	// const fakeRows: any = [
	// 	{
	// 		category: 'Special Diagnostic Procedure',
	// 		benefit: 'CT Scan',
	// 		limit_type: ['percent_abl'],
	// 		limit: [10],
	// 	},
	// 	{
	// 		category: 'Special Diagnostic Procedure',
	// 		benefit: 'Ultrasound',
	// 		limit_type: ['max_session'],
	// 		limit: [14],
	// 	},
	// 	{
	// 		category: 'Wound Care',
	// 		benefit: 'Positron Emiting Technology (PET Scan)',
	// 		limit_type: ['max_amount'],
	// 		limit: [20000],
	// 	},
	// ];

	const columns: any = [
		{ name: 'parent_name', title: 'Category' },
		{ name: 'name', title: 'Benefit' },
		{ 
			name: 'limit_type', 
			title: 'Limit Type',
			getCellValue: row => (row.limits
									? row.limits : [])
		},
		{
			name: 'limit', 
			title: 'Limit',
			getCellValue: row => (row.limits
									? row.limits : [])
		},
	];

	const formattedColumns = {
		limit: ['limit'],
		limit_type: ['limit_type'],
	};

	const columnExtensions = [
		{ columnName: 'limit_type', width: 150 },
		{ columnName: 'limit', width: 150 },
	];

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					{...props}
				/>
			</Grid>
			<Grid item sm={12}>
				<Typography variant="overline" display="block" gutterBottom>
					Limit
				</Typography>	
				{
					!viewOnly ? 
						<React.Fragment>
							<Typography variant="body1" gutterBottom>
								Amount Limit for all procedures
							</Typography>
							<Typography variant="caption" gutterBottom>
								The amount specified for the particular procedure will override this amount
							</Typography>
							<LimitField id="amt_limit" 
								fieldName="amt_limit"
								selectName="amt_limit_unit"
								fieldValue={metadata['amt_limit']}
								selectValue={metadata['amt_limit_unit']}
								fieldOnChange={setSubForm}
								selectOnChange={setSubForm}
							/>
						</React.Fragment> :
						<ViewField 
							label="Amount Limit for all procedures"
							value={
									metadata['amt_limit'] && metadata['amt_limit_unit'] ? 
									metadata['amt_limit'] + ' ' + metadata['amt_limit_unit'] : ''
									}
						/>
				}
			</Grid>
		</React.Fragment>
	);
};

OutpatientForm.defaultProps = defaultProps;