//#region Global Imports
import * as React from 'react';
import Link from '@material-ui/core/Link';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faMinusCircle } from '@fortawesome/free-solid-svg-icons';
import { SidebarList, HeaderLink, ListSection } from 'Components/UI/SidebarList';

interface AddLinkProps {
	name: string;
	value: string;
	onClick: any;
}

export const AddLink: React.FC<AddLinkProps> = (props: AddLinkProps): JSX.Element | null => {
	const { name, value, onClick } = props;

	return (
		<ListItem key={value}>
			<Link onClick={onClick}>
				<ListItemText disableTypography={true} style={{ color: '#3AB77D' }}>
					<FontAwesomeIcon icon={faPlus} /> {name}{' '}
				</ListItemText>
			</Link>
		</ListItem>
	);
};

export interface PackagesSidebarListProps {
	roomAndBoardList: any[];
	riderList: any[];
	coverageList: any[];
	setPopoverOpen?: any;
	selectedBenefitCategory: string;
	setSelectedPlanPackage: any;
	setSelectedBenefitCategory: any;
	deleteBenefitCategory?: any;
	viewOnly?: boolean;
}

const defaultProps: PackagesSidebarListProps = {
	roomAndBoardList: [],
	riderList: [],
	coverageList: [],
	setPopoverOpen: (e, i) => console.log("setPopoverOpen", e, i),
	selectedBenefitCategory: '',
	setSelectedPlanPackage: (e) => console.log("setSelectedPlanPackage", e),
	setSelectedBenefitCategory: (e) => console.log("setSelectedBenefitCategory", e),
	deleteBenefitCategory: (e) => console.log("deleteBenefitCategory", e),
	viewOnly: false
};

export const PackagesSidebarList: React.FC<PackagesSidebarListProps> = (
	props: PackagesSidebarListProps,
): JSX.Element => {
	const {
		selectedBenefitCategory,
		roomAndBoardList,
		riderList,
		coverageList,
		setSelectedPlanPackage,
		setSelectedBenefitCategory,
		deleteBenefitCategory,
		setPopoverOpen,
		viewOnly,
	} = props;

	return (
		<SidebarList>
			<HeaderLink
				sidebarList={roomAndBoardList}
				name={'Room and Board'}
				value={'RBC'}
				selectionKey={selectedBenefitCategory}
				onClick={() => {
					setSelectedPlanPackage('RB');
					setSelectedBenefitCategory('RBC');
				}}
			/>

			<ListSection
				sidebarList={coverageList}
				subheader={'Coverage'}
				nameKey={'name'}
				valueKey={'code'}
				selectionKey={selectedBenefitCategory}
				onClick={(item: any) => {
					setSelectedPlanPackage('CV');
					setSelectedBenefitCategory(`${item.code}`);
				}}
				itemAdornment={!viewOnly ? 
					<FontAwesomeIcon icon={faMinusCircle} style={{ color: '#B0AFB8' }} /> : null
				}
				itemAdornmentOnClick={(item: any) => deleteBenefitCategory(item)}
			>
				{ !viewOnly ? 
					<AddLink
						name={'Add Coverage'}
						value={'coverage'}
						onClick={(e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
							e.persist();
							setPopoverOpen(e, 'CV');
						}}
					/> : null
				}
				
			</ListSection>

			<ListSection
				sidebarList={riderList}
				subheader={'Riders'}
				nameKey={'name'}
				valueKey={'code'}
				selectionKey={selectedBenefitCategory}
				onClick={(item: any) => {
					setSelectedPlanPackage('RI');
					setSelectedBenefitCategory(`${item.code}`);
				}}
				itemAdornment={!viewOnly ? 
					<FontAwesomeIcon icon={faMinusCircle} style={{ color: '#B0AFB8' }} /> : null
				}
				itemAdornmentOnClick={(item: any) => deleteBenefitCategory(item)}
			>
				{ !viewOnly ? 
					<AddLink
						name={'Add Riders'}
						value={'coverage'}
						onClick={(e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
							e.persist();
							setPopoverOpen(e, 'RI');
						}}
					/> : null
				}
			</ListSection>

			<List>
				<ListItem>
					<Link
						onClick={() => {
							setSelectedPlanPackage('EX');
							setSelectedBenefitCategory(`EX`);
						}}
						underline={selectedBenefitCategory === 'EX' ? 'always' : 'hover'}
					>
						<ListItemText disableTypography={true} primary="Exclusions" />
					</Link>
				</ListItem>
				<ListItem>
					<Link
						onClick={() => {
							setSelectedPlanPackage('NT');
							setSelectedBenefitCategory(`NT`);
						}}
						underline={selectedBenefitCategory === 'NT' ? 'always' : 'hover'}
					>
						<ListItemText disableTypography={true} primary="Partner Network" />
					</Link>
				</ListItem>
				<ListItem>
					<Link
						onClick={() => {
							setSelectedPlanPackage('TC');
							setSelectedBenefitCategory(`EL`);
						}}
						underline={selectedBenefitCategory === 'EL' ? 'always' : 'hover'}
					>
						<ListItemText disableTypography={true} primary="Eligibility" />
					</Link>
				</ListItem>
			</List>
		</SidebarList>
	);
};

PackagesSidebarList.defaultProps = defaultProps;