const Select = ({ items, ...restProps  }: any) => {
	const cellSelectClasses = cellSelectStyles();
	return (
		<MaterialUI.Select
			classes={cellSelectClasses}
			style={{ width: '100%', backgroundColor: 'transparent' }}
			renderValue={(selected: any) => {
				if (selected != null) {
					const value: any = items.find((item: any) => {
						return item.value === selected;
					});
					if (value) {
						return value.label;
					} else {
						return '';
					}
				}
				return '';
			}}
			{...restProps}
		>
			{items.map((item: any, i: number) => (
				<MaterialUI.MenuItem key={i} value={item.value}>
					{item.label}
				</MaterialUI.MenuItem>
			))}
		</MaterialUI.Select>
	);
};

const MultipleSelect = ({ id, text, value, items, ...restProps }: any) => {
	const cellSelectClasses = cellSelectStyles();
	return (
		<MaterialUI.Select
			multiple
			input={<MaterialUI.Input id={id} />}
			value={value}
			renderValue={(selected: any) => {
				if (text !== undefined) {
					const value: any = items.find((item: any) => {
						return item.value === text;
					});
					if (value) {
						return value.label;
					} else {
						return '';
					}
				}

				const selectedNames: string[] = [];

				selected.map((item: any) => {
					const value: any = items.find((item2: any) => {
						return item2.value === item;
					});
					selectedNames.push(value.label);
				});
				return selectedNames.join(', ');
			}}
			style={{ width: '100%', backgroundColor: 'transparent' }}
			classes={cellSelectClasses}
			{...restProps}
		>
			{items.map((item: any, i: number) => (
				<MaterialUI.MenuItem key={i} value={item.value}>
					<MaterialUI.Checkbox checked={value.indexOf(item.value) > -1} />
					<MaterialUI.ListItemText primary={item.label} />
				</MaterialUI.MenuItem>
			))}
		</MaterialUI.Select>
	);
};

const BenefitsFilterCell = ({ filter, onFilter, filteringEnabled, classes }: any) => {
	if (filteringEnabled) {
		return (
			<MaterialUI.TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
				<MaterialUI.OutlinedInput
					classes={classes}
					style={{ width: '100%' }}
					value={filter ? filter.value : ''}
					labelWidth={0}
					onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
					placeholder=""
				/>
			</MaterialUI.TableCell>
		);
	} 
	return null;
}
