//#region Global Imports
import * as React from 'react';
import AddIcon from '@material-ui/icons/Add';
import Tabs from '@material-ui/core/Tabs';
import Button from '@material-ui/core/Button';
import { PlanTypeTab } from './PlanTypeTab';

export interface PlanTypeTabBarProps {
	planTypeList: any[];
	setSelectedPlanType: any;
	selectedPlanType: string;
	setModalOpen: any;
	viewOnly: boolean;
}

export const PlanTypeTabBar: React.FC<PlanTypeTabBarProps> = (
	props: PlanTypeTabBarProps,
): JSX.Element => {
	const { selectedPlanType, viewOnly, planTypeList, setSelectedPlanType, setModalOpen } = props;

	return (
		<React.Fragment>
			{planTypeList.length > 0 ? (
				<Tabs value={selectedPlanType} style={{ marginTop: '40px' }}>
					{/*<Tabs value={selectedPlanType} variant="scrollable" scrollButtons="on">*/}
					{planTypeList.map((item: any) => {
						let term = 'benefits';
						if (item.count === 1) {
							term = 'benefit';
						}
					
						return item.name && item.code && item.included ? (
							<PlanTypeTab
								value={item.code}
								label={item.name}
								subtitle={item.code === 'BASE' ? `${item.count} ${term}` : `+${item.count} ${term}`}
								onClick={() => setSelectedPlanType(item.code)}
							/>) : null;
						}
					)}
					{
						!viewOnly ?
							<div style={{ display: 'inline-flex', alignItems: 'center' }}>
								<Button
									variant="contained"
									color="primary"
									onClick={setModalOpen}
								>
									<AddIcon />
									Add Plan Type
								</Button>
							</div> : null
					}
				</Tabs>
			) : null}
		</React.Fragment>
	);
};
