	const filterNodesByLevel = (item: any, nodes: any[], level: number) => {
		if (item.level !== level) {
			return true;
		}

		if (item.tree_path[2] !== 'BASE') {
			const baseNode = nodes.find ((baseItem) => {
	          return (
	            baseItem.name === item.name &&
	            baseItem.code === item.code &&
	            baseItem.level === item.level &&
	            baseItem.tree_path[2] === 'BASE'
	          )
	        });

			if (baseNode) {
				if (!baseNode.included && item.included) {
					return true;
				} else if (baseNode.included && item.edited) {
					return true;
				} else if (baseNode.included && item.included && !item.edited) {
					const children = item.children.filter((child) => {
						const found = nodes.find((node) => child === node.id);

						return found ? true : false;
					});

					if (children.length > 0) {
						return true;
					}
				}
			}
		} else {
			if (item.included) {
				return true;
			}
		}

		return false;
	}

	const getNodesPayload = (nodeData: any, clientData: any) => {
		// Filter out benefits that aren't included in base plan
		// Making several filter loops (backwards from sub-benefit to plan package) to make sure
		// children are accounted for
		let nodes = [...nodeData];

		for (let x = 8; x > 3; x--) {
			nodes = nodes.filter ((item: any) => filterNodesByLevel(item, nodes, x));
			nodes = nodes.map ((item: any) => {
				let newItem = Object.assign({}, item);

				newItem.children = newItem.children.filter(child =>
		          nodes.find((node) => node.id === child) ? true : false
		        );
		        return newItem;
			});
		}

		nodes = nodes.map ((item: any) => {
			let newItem = Object.assign({}, item);

			newItem.children = newItem.children.filter(child =>
	          nodes.find((node) => node.id === child) ? true : false
	        );

	        if (newItem.code === 'CT') {
	        	newItem['principals_count'] = parseInt(clientData.principalData.numOfPrincipals);
	        	newItem['male_principals_count'] = parseInt(clientData.principalData.malePrincipals);
	        	newItem['female_principals_count'] = parseInt(clientData.principalData.femalePrincipals);
	        	newItem['dependents_count'] = parseInt(clientData.principalData.numOfDependents);
	        	newItem['male_dependents_count'] = parseInt(clientData.principalData.maleDependents);
	        	newItem['female_dependents_count'] = parseInt(clientData.principalData.femaleDependents);
	        }

	        if (newItem.code == 'OD' || newItem.code == 'CB') {
				if (newItem['custom_metadata']['partner_network_list'] && newItem['custom_metadata']['partner_network_list'].length > 0) {

					const newPartnerNetworkList = [...item['custom_metadata']['partner_network_list']];

					newItem['custom_metadata']['partner_network_list'] = newPartnerNetworkList.reduce((result, partner: any) => {
						if (partner['included'] && partner['included'] === true) {
							delete partner['included'];
							result.push(partner);
						}
						return result;
					}, new Array());
				}
			}

			delete newItem.tree_path;
			delete newItem.tree_id;
			delete newItem.included;
			delete newItem.count;
			delete newItem.level;
			delete newItem.edited;
			delete newItem.node_path;
			return newItem;
		});

		return nodes;
	}

