//#region Global Imports
import * as React from 'react';

import { BenefitsTable } from '../../BenefitsTable';

import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';

//import { Components } from '@hims/core';
//#endregion Global Imports


import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const DefaultForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { viewOnly, formHeader, rows } = props;

	const columns: any = [
		{ name: 'name', title: 'Category' },
		{ name: 'description', title: 'Description' },
	];

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable rows={rows} columns={columns} />
			</Grid>
		</React.Fragment>
	);
};

DefaultForm.defaultProps = defaultProps;
