//#region Global Imports
import * as React from 'react';

import { BenefitsTable } from '../../BenefitsTable';

import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';

import Typography from '@material-ui/core/Typography';

import {
	PesoField,
} from '../../ComboField';
import { ViewField } from '../../ViewField';

//import { Components } from '@hims/core';
//#endregion Global Imports


import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const SpecialDiagnosticForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, rows, viewOnly } = props;

	console.log(rows);

	const fakeRows: any = [
		{
			procedure: 'Chest X-Ray for chest pains',
			limit_type: ['mbl_limit'],
			limit: [null],
		},
		{
			procedure: 'Abdominal X-Ray for gastrointestinal diseases',
			limit_type: ['abl_limit'],
			limit: [null],
		},
		{
			procedure: 'Electrocardingram for heart disease',
			limit_type: ['pec_limit'],
			limit: [1000],
		},
		{
			procedure: 'Ultrasound',
			limit_type: ['disease_limit'],
			limit: [1000],
		},
	];

	const columns: any = [
		{ name: 'procedure', title: 'Procedure' },
		{ name: 'limit_type', title: 'Limit Type' },
		{ name: 'limit', title: 'Limit' },
	];

	const formattedColumns = {
		limit: ['limit'],
		limit_type: ['limit_type'],
	};

	const columnExtensions = [
		{ columnName: 'limit_type', width: 150 },
		{ columnName: 'limit', width: 150 },
	];

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					rows={fakeRows}
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
				/>
			</Grid>
			<Grid item sm={12}>
				<Typography variant="overline" display="block" gutterBottom>
					Limit
				</Typography>
				{
					!viewOnly ? 
						<React.Fragment>
							<Typography variant="body1" gutterBottom>
								Peso Limit for all procedures
							</Typography>
							<Typography variant="caption" gutterBottom>
								The amount specified for the particular procedure will override this amount
							</Typography>
							<PesoField id="amt_limit" />
						</React.Fragment> :
						<ViewField 
							label="Peso Limit for all procedures"
							value={'PHP 2,000'}
						/>
				}
			</Grid>
		</React.Fragment>
	);
};

SpecialDiagnosticForm.defaultProps = defaultProps;