//#region Global Imports
import * as React from 'react';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import { CategoryForm } from '../../Components/CategoryForm';
import { PackagesSidebarList } from '../../Components/PackagesSidebarList';
import { BenefitDetailsForm } from '../../Components/BenefitDetailsForm';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileDownload, faPrint } from '@fortawesome/free-solid-svg-icons';

//#endregion Global Imports

//#region Local Imports
//#endregion Local Imports

//#region Interface Imports
// import { Store } from 'Components/Stores/IStore';
// import { ICreateProposal } from 'Pages/CreateProposal/ICreateProposal';
// import { CreateProposalActions } from '../../CreateProposalActions';
//#endregion Interface Imports

import Typography from '@material-ui/core/Typography';

import '@hims/core/dist/index.css';


interface SMEPlanProps {
	contractNode: any;
	categoryNode: any;
	rows: any[];
	roomAndBoardList: any[];
	coverageList: any[];
	riderList: any[];
	planTypeList: any[];
	selectedPlanType: string;
	selectedPlanPackage: string;
	selectedBenefitCategory: string;
	SetSelectedPlanPackage: any;
	SetSelectedBenefitCategory: any;
	classes: any;
	submitMode?: boolean;
}

export const StandardSMEPlan: React.FC<SMEPlanProps> = (props: SMEPlanProps): JSX.Element => {
	const { 
		contractNode,
		categoryNode,
		rows,
		roomAndBoardList, 
		coverageList, 
		riderList, 
		planTypeList,
		selectedPlanType,
		selectedBenefitCategory,
		SetSelectedPlanPackage, 
		SetSelectedBenefitCategory,
		classes,
		submitMode
	} = props;

	return (
		<Grid container className={classes.gridContainer}>
			<Grid item sm={6}>
				{	!submitMode ?
						<Typography gutterBottom
							className={classes.sectionTitle}
						>
							Standard SME Plan
						</Typography> : 
						<Typography gutterBottom
							className={classes.sectionTitle}
						>
							Proposal
						</Typography>
				}
			</Grid>
			<Grid item sm={6} style={{textAlign: 'right'}}>
				<FontAwesomeIcon size="lg" icon={faFileDownload} style={{marginTop: 10, marginRight: 10}} />
				<FontAwesomeIcon size="lg" icon={faPrint} style={{marginTop: 10}} />
			</Grid>
								
			<BenefitDetailsForm 
				classes={classes}
				contractNode={contractNode}
				viewOnly
				isSME
			/>

			<Grid item sm={12} style={{marginTop: '20px', marginBottom: '10px'}}>
				<Typography variant="h6">{planTypeList.map((item: any) => {
					let term = 'benefits';
					if (item.count === 1) {
						term = 'benefit';
					}

					return item.code === 'BASE' ? `${item.count} ${term}` : null
				})}
				</Typography>
			</Grid>

			<Grid item sm={12}>
				<Divider variant="middle" />
			</Grid>

			<Grid item sm={2}>
				<PackagesSidebarList
					selectedBenefitCategory={selectedBenefitCategory}
					roomAndBoardList={roomAndBoardList}
					riderList={riderList}
					coverageList={coverageList}
					setSelectedPlanPackage={SetSelectedPlanPackage}
					setSelectedBenefitCategory={SetSelectedBenefitCategory}
					viewOnly
				/>
			</Grid>

			<Grid item sm={10}>
				<CategoryForm
					selectedPlanType={selectedPlanType}
					selectedBenefitCategory={selectedBenefitCategory}
					subFormNode={categoryNode}
					rows={rows}
					viewOnly
				/>
			</Grid>
		</Grid>
	);
};
