//#region Global Imports
import * as React from 'react';
import Grid from '@material-ui/core/Grid';
import {Details} from '../Details';
import {StandardBenefitPlan} from '../Benefits/StandardBenefitPlan';
import {StandardSMEPlan} from '../Benefits/StandardSMEPlan';
//#endregion Global Imports

//#region Local Imports
//#endregion Local Imports

//#region Interface Imports
// import { Store } from 'Components/Stores/IStore';
import { ICreateProposal } from 'Pages/CreateProposal/ICreateProposal';
// import { CreateProposalActions } from '../../CreateProposalActions';
//#endregion Interface Imports

import Typography from '@material-ui/core/Typography';

import '@hims/core/dist/index.css';


interface SubmitProps {
	invalidFields: string[];
}

export const Submit: React.FC<ICreateProposal.IProps & SubmitProps> = (props: ICreateProposal.IProps & SubmitProps): JSX.Element => {
	const {classes, contractType} = props;

	return (
		<React.Fragment>
			<Grid container className={classes.gridContainer}>
				<Grid item sm={12}>
					<Typography className={classes.sectionTitle}>
						Review Proposal
					</Typography>
					<Typography gutterBottom className={classes.subSectionTitle}>
						Please review the proposal before submitting.
					</Typography>
				</Grid>
			</Grid>

			<Details {...props} submitMode={true} />
			{ contractType === 'benefit' ? 	<StandardBenefitPlan {...props} submitMode={true} /> : 
											<StandardSMEPlan {...props} submitMode={true} />
			}
			
		</React.Fragment>
	);
};
