//#region Global Imports
import * as React from 'react';
import { useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Popover from '@material-ui/core/Popover';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
// import { UnderwritingContractsApi, UnderwritingBenefitsApi } from '@hims/api';
import { PlanTypeTabBar } from '../../Components/PlanTypeTabBar';
import { CategoryForm } from '../../Components/CategoryForm';
import { PackagesSidebarList } from '../../Components/PackagesSidebarList';
// import Container from '@material-ui/core/Container';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { BenefitDetailsForm } from '../../Components/BenefitDetailsForm';
import { AddPlanTypeModal } from '../../Components/Modal/AddPlanTypeModal';
import { LimitTypeItems } from '../../Components/BenefitsTable/DropdownConstants';
import { generatePlanNodes } from '../../CreateProposalPage';
//#endregion Global Imports

//#region Local Imports
//#endregion Local Imports

//#region Interface Imports
// import { ICreateProposal } from 'Pages/CreateProposal/ICreateProposal';
//#endregion Interface Imports

import Typography from '@material-ui/core/Typography';

import '@hims/core/dist/index.css';


const addLinkStyles = makeStyles((theme: Theme) =>
	createStyles({
		root: {
			maxWidth: 360,
			backgroundColor: theme.palette.background.paper,
		},
	}),
);

interface BenefitPlanProps {
	benefitNodes: any[];
	planTypeList: any[];
	contractNode: any;
	categoryNode: any;
	roomAndBoardList: any[];
	coverageList: any[];
	riderList: any[];
	rows: any[];
	selectedPlanType: string;
	selectedPlanPackage: string;
	selectedBenefitCategory: string;
	classes: any;
	submitMode?: boolean;
	SetBenefitNodes: any;
	SetSelectedPlanType: any;
	SetSelectedPlanPackage: any;
	SetSelectedBenefitCategory: any;
	SetNewNodeCounter: any;
	viewOnly?: boolean;
	newNodeCounter: number;
}

export const StandardBenefitPlan: React.FC<BenefitPlanProps> = (props: BenefitPlanProps): JSX.Element => {
	const { 
		benefitNodes,
		contractNode,
		categoryNode,
		rows,
		planTypeList, 
		roomAndBoardList, 
		coverageList, 
		riderList, 
		selectedPlanType,
		selectedPlanPackage,
		selectedBenefitCategory,
		SetBenefitNodes, 
		SetSelectedPlanType, 
		SetSelectedPlanPackage, 
		SetSelectedBenefitCategory,
		SetNewNodeCounter,
		classes,
		submitMode,
		viewOnly,
		newNodeCounter
	} = props;

	// const isInitialMount = useRef(true);
	// const [rootID] = useState('5da3fd4067fc03960173a18b');
	const [planTypeModal, setPlanTypeModal] = useState(false);
	// const [confirmationModal, setConfirmationModal] = useState(false);
	// const [confirmationSubmit, setConfirmationSubmit] = useState('');
	// const [confirmationMessage, setConfirmationMessage] = useState('');
	// const [successModal, setSuccessModal] = useState(false);
	// const [successMessage, setSuccessMessage] = useState('');
	const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
	const [popoverList, setPopoverList] = useState<any[]>([]);
	const popoverOpen = Boolean(anchorEl);

	const addLinkClasses = addLinkStyles();

	// const contractsApiInstance = new UnderwritingContractsApi();
	// const benefitsApiInstance = new UnderwritingBenefitsApi();

	
	const handleChangeBenefitForm = (e: any) => {
		let nodes = [...benefitNodes];

		const index = nodes.findIndex(item => 
		    item.level === 2 && 
		    item.code === 'CT'
		);

		let metadata = {};
		metadata[e.target.name] = e.target.value;

		if (nodes[index]['custom_metadata']) {
			nodes[index]['custom_metadata'] = Object.assign ({}, nodes[index]['custom_metadata'], metadata);

			SetBenefitNodes(nodes);
		}
	}

	const handleChangeSubForm = (e: any, field: string = 'custom_metadata', fieldIndex: number = -1) => {
		handleChangeSubForm2(e.target.name, e.target.value, field, fieldIndex);
	}

	const handleChangeSubForm2 = (name: string, value: any, field: string = 'custom_metadata', fieldIndex: number = -1) => {
		let nodes = [...benefitNodes];

		let index = 0;

		if (selectedBenefitCategory === 'RBC') {
			index = nodes.findIndex(item => 
		      item.code === selectedPlanPackage &&
		      item.tree_path[2] === selectedPlanType && 
		      item.tree_path[3] === selectedPlanPackage 
		    );
		} else if (selectedBenefitCategory === 'NT') {
			index = nodes.findIndex(item => 
				item.level === 4 && 
				item.code === selectedBenefitCategory &&
			    item.tree_path[2] === selectedPlanType && 
			    item.tree_path[3] === selectedBenefitCategory 
		    );
		} else {
			index = nodes.findIndex(item => 
				item.level === 5 && 
				item.code === selectedBenefitCategory &&
				item.tree_path[2] === selectedPlanType && 
				item.tree_path[3] === selectedPlanPackage && 
				item.tree_path[4] === selectedBenefitCategory
			);
		}
		
		let data: any = {};

		if (field === 'limits' && fieldIndex === -1) {
			data = value;
		} else {
			data[name] = value;
		}

		if (nodes[index][field]) {
			if (fieldIndex > -1) {
				nodes[index][field][fieldIndex] = Object.assign ({}, nodes[index][field][fieldIndex], data);
			} else {
				if (Array.isArray(data)) {
					nodes[index][field] = [...data];
				} else {
					nodes[index][field] = Object.assign ({}, nodes[index][field], data);
				}
			}

			SetBenefitNodes(nodes);
		}
	}

	const handleCreateNode = (name: string = '', description: string = '', data: object = {}, parent: any | null = null) => {
		let nodes = [...benefitNodes];
		let indexes: number[] = [];
		let templateNodes: object[] = [];

		if (parent) {
			// if Relationship
			const index = nodes.findIndex ((item) => {
				return 	item.level === 7 && 
						item.name === parent.name &&
						item.code === parent.code &&
				        item.tree_path[2] === parent.tree_path[2] &&
				        item.tree_path[3] === parent.tree_path[3] &&
				        item.tree_path[4] === parent.tree_path[4] &&
				        item.tree_path[5] === parent.tree_path[5] &&
				        item.tree_path[6] === parent.tree_path[6];
			});

			indexes = [index];

			templateNodes = nodes.reduce((result, item) => {
		      if ( item.level === 8 &&
		        item.tree_path[2] === selectedPlanType  &&
		        item.tree_path[3] === selectedPlanPackage &&
		        item.tree_path[4] === selectedBenefitCategory
		      ){
		      	result.push(item);
		      }
		      return result;
		    }, new Array());
		} else {
			// if Civil Status
			indexes = nodes.reduce((result, item, i) => {
		      if ( item.level === 6 &&
		        item.tree_path[2] === selectedPlanType  &&
		        item.tree_path[3] === selectedPlanPackage &&
		        item.tree_path[4] === selectedBenefitCategory 
		      ){
		      	result.push(i);
		      }
		      return result;
		    }, new Array());

		    templateNodes = nodes.reduce((result, item) => {
		      if ( item.level === 7 &&
		        item.tree_path[2] === selectedPlanType  &&
		        item.tree_path[3] === selectedPlanPackage &&
		        item.tree_path[4] === selectedBenefitCategory
		      ){
		      	result.push(item);
		      }
		      return result;
		    }, new Array());
		}

	    if (templateNodes.length > 0) {
	    	console.log(templateNodes);
	    	let count = 0;

			const benefitData = {
			    name: name,
			    code: name.replace(/[^A-Z]/g, ''),
			    description: description,
			    metadata: {},
			    metadata_schema: {},
			    included: true,
			    children: [],
			    custom_metadata: Object.assign({title: name}, data),
			    custom_metadata_schema: {
			      properties: {
			        title: {type: 'string'},
			      },
			      required: [],
			    },
			 };

			indexes.map((index) => {
		    	const newID = "NODE-NEW-"+(newNodeCounter+count).toString().padStart(3, '0');
		    	const baseNode = Object.assign ({}, templateNodes[0]);

		    	let moreData = {
		    		tree_path: [
		    			"MCT",
		    			"CT",
		    			selectedPlanType,
		    			selectedPlanPackage,
		    			selectedBenefitCategory,
		    			nodes[index].code,
		    			name.replace(/[^A-Z]/g, '')
		    		],
		    		id: newID,
			    	level: 7,
			    	type: 'Benefit'
		    	};

		    	if (parent) {
		    		moreData['tree_path'].splice(5, 0, 'DP');
		    		moreData['level'] = 8;
		    		moreData['type'] = 'Sub-Benefit';
		    	}

				const newNode = Object.assign({}, baseNode, benefitData, moreData);

				let newChildren = [...nodes[index].children];
				newChildren.push(newID);

				nodes[index]['children'] = newChildren;
				
				nodes.push(newNode);
				count++;
			});

			SetBenefitNodes(nodes);
			SetNewNodeCounter(newNodeCounter + count);
	    }
		
	}

	const handleAddExclusion = (node: object) => {
		console.log("handleAddExclusion", node);
	}

	const handleDeleteExclusion = (node: object) => {
		console.log("handleDeleteExclusion", node);
		
	}

	const handleChangeRowMetadata = (e: any, id: number) => {
		handleChangeRowMetadata2(e.target.name, e.target.value, id);
	}

	const handleChangeRowMetadata2 = (name: string, value: any, id: number, metaIndex: number = -1, metaName: string = '') => {
		let nodes = [...benefitNodes];

		const index = nodes.findIndex(item => 
			item.id === id
		);

		let metadata = {};	

		if (metaIndex > -1 && metaName) {
			metadata = Object.assign({}, nodes[index]['custom_metadata']);
			if (metadata[name]) {
				console.log(metadata[name][metaIndex], metaName, value);

				metadata[name][metaIndex][metaName] = value;

				if (metaName === 'included') {
					if (value === false) {
						handleAddExclusion(metadata[name][metaIndex]);
					} else {
						handleDeleteExclusion(metadata[name][metaIndex]);
					}
				}
			}
		} else {
			metadata[name] = value;
		}

		if (nodes[index]['custom_metadata']) {
			nodes[index]['custom_metadata'] = Object.assign ({}, nodes[index]['custom_metadata'], metadata);

			if (nodes[index].tree_path[2] !== 'BASE') {
				nodes[index]['edited'] = true;
			}

			SetBenefitNodes(nodes);
		}
	}

	const handleChangeRowLimits = (e: any, id: number, i: number) => {
		let nodes = [...benefitNodes];

		const index = nodes.findIndex(item => 
			item.id === id
		);

		const limValue = e.target.value;
		const limName = e.target.name;
		
		let limits = [...nodes[index]['limits']];

		if (nodes[index]['limits']) {
			if (limName === 'limit_type') {
				const newLimits = limValue.reduce((result, val) => {
					const limType = LimitTypeItems.find((limType) => limType.label === val);

					if (limType) {
						const found = limits.find((item) => item.type === limType.type && item.limit_unit === limType.limit_unit);

						if (found) {
							result.push(found);
						} else {
							const input = {
								type: limType.type,
								limit: 0,
								limit_unit: limType.limit_unit,
								daily_limit: 0,
							};

							result.push(input);	
						}
					} 

					return result;
				}, new Array());

				limits = newLimits;
			} else if (['limit','daily_limit'].includes(limName)) {
				limits[i][limName] = parseFloat(limValue);
			}

			nodes[index]['limits'] = limits;

			SetBenefitNodes(nodes);
		}
	}


	const handleChangeRowSelection = (rowObjs: any[]) => {
		let nodes = [...benefitNodes];

		rowObjs.map ((rowObj) => {
			const index = nodes.findIndex(item => 
				item.id === rowObj.id
			);
			if (nodes[index]['included'] !== rowObj.included) {
				nodes[index]['included'] = rowObj.included;
			}
		})

		SetBenefitNodes(nodes);
	}

	const handlePackagePopoverOpen = (
		e: React.MouseEvent<HTMLButtonElement, MouseEvent>,
		planPackage: string,
	) => {
		const currentTarget = e.currentTarget;

		let nodes: any[] = [];
	
		if (planPackage === 'CV') {
			nodes = coverageList.filter((item: any) => {
				return !item.included;
			});
		}

		if (planPackage === 'RI') {
			nodes = riderList.filter((item: any) => {
				return !item.included;
			});
		}

		setPopoverList(nodes);
		setAnchorEl(currentTarget);
	}
	const handlePlanModalOpen = () => {
		setPlanTypeModal(true);
	}

	const handlePlanModalClose = () => {
		setPlanTypeModal(false);
	}

	const handlePopoverClose = () => {
		setAnchorEl(null);
	}

	const handlePopoverClick = (planType: string, planPackage: string, benefitCategory: string) => {
		addBenefitCategory(planType, planPackage, benefitCategory);
	}

	const deleteBenefitCategory = (category: any) => {
		const nodePath = category.tree_path;

		const nodes = benefitNodes.map((item: any) => {
			
			if (
				item.tree_path[1] === undefined ||
				item.tree_path[2] === undefined ||
				item.tree_path[3] === undefined ||
				item.tree_path[4] === undefined
			) {
				return item;
			}

			if (
				item.level <= 6 &&
				item.tree_path[1] === nodePath[1] &&
				item.tree_path[2] === nodePath[2] &&
				item.tree_path[3] === nodePath[3] &&
				item.tree_path[4] === nodePath[4]
			) {
				let newItem = Object.assign({}, item);
				newItem['included'] = false;
				return newItem;
			};

			return item;
		});

		SetBenefitNodes(nodes);
	}

	const addBenefitCategory = (planType: string, planPackage: string, benefitCategory: string) => {
		let nodes = [...benefitNodes];

		const index = nodes.findIndex(item => 
			item.level === 5 && 
			item.code === benefitCategory &&
			item.tree_path[2] === planType && 
			item.tree_path[3] === planPackage && 
			item.tree_path[4] === benefitCategory
		);

		nodes[index]['included'] = true;
		SetBenefitNodes(nodes);
		setAnchorEl(null);
	}

	// const deletePlanType() {}

	const addPlanType = (data: any) => {
		let nodes = [...benefitNodes];
		
		const {planNodes, planTypeID} = generatePlanNodes(nodes, data['name'], data['code']);

		const index = nodes.findIndex(item => 
			item.level === 2 && 
			item.code === 'CT'
		);

		nodes[index]['children'].push(planTypeID);

		SetBenefitNodes([...nodes, ...planNodes]);
	}

	return (
		<div>
			<Grid container className={classes.gridContainer}>
			
				<Grid item sm={12}>
					{	!submitMode && !viewOnly ?
							<React.Fragment>
								<Typography gutterBottom
									style={{ marginRight: '20px' }}
									className={classes.sectionTitle}
								>
									Standard Benefit Plan
								</Typography>
								<Typography display="inline" className={classes.subSectionTitle} gutterBottom>
									You are currently customizing this benefit plan
								</Typography>
							</React.Fragment> : null
					}
					{
						submitMode ? 	
							<Typography gutterBottom
								className={classes.sectionTitle}
							>
								Customized Proposal
							</Typography> : null
					}
					{
						viewOnly ? 
							<Typography gutterBottom
								className={classes.sectionTitle}
							>
								Review Benefits
							</Typography> : null
					}
				</Grid> 

				<BenefitDetailsForm 
					classes={classes}
					contractNode={contractNode}
					setChangeBenefitForm={handleChangeBenefitForm}
					viewOnly={viewOnly ? viewOnly : false}
				/>

				<Grid item sm={12}>
					<PlanTypeTabBar
						selectedPlanType={selectedPlanType}
						planTypeList={planTypeList}
						setSelectedPlanType={SetSelectedPlanType}
						setModalOpen={handlePlanModalOpen}
						viewOnly={viewOnly ? viewOnly : false}
					/>
				</Grid>

				<Grid item sm={12}>
					<Divider variant="middle" />
				</Grid>

				<Grid item sm={2}>
					<PackagesSidebarList
						selectedBenefitCategory={selectedBenefitCategory}
						roomAndBoardList={roomAndBoardList}
						riderList={riderList}
						coverageList={coverageList}
						setSelectedPlanPackage={SetSelectedPlanPackage}
						setSelectedBenefitCategory={SetSelectedBenefitCategory}
						deleteBenefitCategory={deleteBenefitCategory}
						setPopoverOpen={handlePackagePopoverOpen}
						viewOnly={viewOnly ? viewOnly : false}
					/>
				</Grid>

				<Grid item sm={10}>
					<CategoryForm
						selectedPlanType={selectedPlanType}
						selectedBenefitCategory={selectedBenefitCategory}
						setRowSelection={!viewOnly ? handleChangeRowSelection : () => {}}
						setRowLimits={!viewOnly ? handleChangeRowLimits : () => {}}
						setRowMetadata={!viewOnly ? handleChangeRowMetadata : () => {}}
						setRowMetadata2={!viewOnly ? handleChangeRowMetadata2 : () => {}}
						setSubForm={!viewOnly ? handleChangeSubForm : () => {}}
						setSubForm2={!viewOnly ? handleChangeSubForm2 : () => {}}
						createNode={!viewOnly? handleCreateNode : () => {}}
						subFormNode={categoryNode}
						rows={rows}
						viewOnly={viewOnly ? viewOnly : false}
					/>
				</Grid>

				<Popover
					open={popoverOpen}
					anchorEl={anchorEl}
					onClose={handlePopoverClose}
					anchorOrigin={{
						vertical: 'bottom',
						horizontal: 'left',
					}}
					transformOrigin={{
						vertical: 'top',
						horizontal: 'left',
					}}
				>
					<List classes={addLinkClasses}>
						{popoverList.length > 0 ? (
							popoverList.map((item: any) =>
								item['name'] && item['code'] ? (
									<ListItem
										button
										key={`${item['code']}`}
										onClick={() =>
											handlePopoverClick(
												item['tree_path'][2] !== undefined
													? item['tree_path'][2]
													: null,
												item['tree_path'][3] !== undefined
													? item['tree_path'][3]
													: null,
												item['tree_path'][4] !== undefined
													? item['tree_path'][4]
													: null,
											)
										}
									>
										<ListItemText
											disableTypography={true}
											primary={`${item['name']}`}
										/>
									</ListItem>
								) : null,
							)
						) : (
							<ListItem>
								<ListItemText disableTypography={true} primary={'Cannot add more.'} />
							</ListItem>
						)}
					</List>
				</Popover>
			</Grid>

			<AddPlanTypeModal onClose={handlePlanModalClose} isModalOpen={planTypeModal} onSubmit={addPlanType} />
		</div>
	);
};
