export const displayValue = (inputValue: number, inputUnit: string) => {
	let unit = 'PHP';
	let value = 0;

	if (inputUnit) {
		unit = inputUnit;
	} 

	if (inputValue) {
		value = inputValue;
	} else {
		return '-';
	}

	return GlobalFunction.formatNumber(value) + " " + unit;
}

